@import "vars";

html,
body,
ul,
li,
button,
input,
textarea,
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Ubuntu", sans-serif;
  margin: 0;
  padding: 0;
  border-width: 0;
  font-size: 16px;
  line-height: inherit;
  outline-color: $highlight-color;
}

body {
  line-height: 1.5;
}

strong {
  font-weight: 500;
}

address {
  font-style: inherit;
}
